@import "./styles/color";
@import "./styles/typography";

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.coutrylist__container {
  width: 100%;
  margin-top: 40px;
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  row-gap: 40px;
}

.filter__container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 20vh;
}

.back__button {
  margin: 40px;
  padding: 0.5em 2em;
  width: auto;
  box-shadow: 0px 2px 5px rgba(61, 61, 61, 0.1);
  background-color: var(--bg-color-elements);
  color: var(--font-color);
  outline: none;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 1440px) {
  .coutrylist__container {
    margin-top: 0;
    grid-template-columns: repeat(4, 1fr);
  }
  .filter__container {
    margin: 0 40px;
    flex-direction: row;
    justify-content: space-between;
  }
}
